.container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    padding: 15px;
    width: 100px;
    height: 100px;
    flex-shrink: 0;

    span {
        font-size: 40px;
        font-weight: bold;
    }

    img {
        max-width: 50px;
    }
}
